.post {
  max-width: 600px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);

  margin-bottom: 20px;
  text-align: left;

  transition: all 0.4s;
}

.post:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  transform: scale(1.01);
}

.post__header {
  margin: 0;
  padding: 14px 20px;

  background-color: #000;
  color: #fff;
}

.post__content-wrapper {
  padding: 20px 20px;
}

.post__body {
  margin: 6px 0;
  padding: 0;
}
