.post_comment {
  padding: 0 10px;
  border-bottom: 1px solid #f1f1f1;
}

.post__comment:last-child {
  background-color: red;
}

.post__comment-body {
  margin: 20px 0 10px;
  padding: 0;
}

.post__comment-author {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;

  font-size: 14px;
  color: #929292;
}
